import React, { Component } from "react";
import './footer.css';

export default class Footer extends Component {
  displayCopyright = () => {
    let today = new Date();
    let year = today.getFullYear();
    return (
      <p className="date">Copyright &copy; {year}</p>
    )
  } 
  render() {
    return(
      <footer className="footer">
         {this.displayCopyright()}
      </footer>
    )
  }
}


